import React, { useEffect } from "react";
import { useLocation } from "react-router-dom";
import { Card } from "flowbite-react";
import { Fade } from "react-awesome-reveal";
import { projectsList } from "../../../config";

interface Projects {}

interface Projectcard {
  className?: string;
  children?: React.ReactNode;
  imgSrc?: string;
}
function Projectcard({ children, imgSrc, className }: Projectcard) {
  return (
    <Card className={className} imgSrc={imgSrc} imgAlt="loding">
      {children}
    </Card>
  );
}

export default function Projects({}: Projects) {
  const location = useLocation();

  useEffect(() => {
    console.dir(location);
  }, []);

  return (
    <section
      id="projects"
      className="min-h-screen w-screen dark:bg-primary dark:text-gray-200 "
    >
      <div className="container mx-auto py-10">
        <h1 className="text-gray-300 font-bold text-4xl py-5 m-1 border-b-2 dark:border-secondery rounded-sm">
          Projects
        </h1>
        <div className="grid grid-cols-2 lg:grid-cols-4 gap-5">
          {projectsList.map((p, i) => (
            <Fade
              delay={Math.random() * 500}
              direction="right"
              key={i}
              triggerOnce
              className=""
            >
              <a href={p.link} target={"_blank"} rel={"noreferrer"}>
                <Projectcard className="" imgSrc={p.img}>
                  <h5 className="text-2xl font-bold tracking-tight text-gray-900 dark:text-gray-200">
                    {p.title}
                  </h5>
                  <p className="font-normal text-gray-700 dark:text-gray-400">
                    {p.description}
                  </p>
                </Projectcard>
              </a>
            </Fade>
          ))}
        </div>
      </div>
    </section>
  );
}
